// Section Hero
section#hero {
	width: 100%;
	padding: 150px 0px 130px 0px;
	color: $white;
	background-position: center;
	background-size: cover;
	text-align: center;
	.title {
		font-size: get-size(map45);
		margin-bottom: 20px;
		margin-top: 0px;
	}
	.subtitle {
		font-size: get-size(map25);
		margin: 5px;
		@include bp-md {
			display: inline-flex;
			span {
				margin-right: 5px;
			}
		}
	}
	.text {
		font-size: get-size(map16);
	}
}

// Section Clients
section#clients {
	width: 100%;
	background-color: $white;
	padding: 50px 0px 60px 0px;
	text-align: center;
	.container-flex {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		.client {
			width: 50%;
			@include bp-sm {
				width: percentage(1/3);
			}
			@include bp-md {
				width: percentage(1/6);
			}
			display: flex;
			justify-content: center;
			align-items: center;
			margin-top: 50px;
			img {
				height: 80px;
				@include bp-sm {
					width: auto;
				}
				@include bp-lg {
					width: auto;
					height: 100px;
				}
			}
		}
	}
	h2.section-title {
		font-size: get-size(map28);
		font-weight: normal;
	}
}

// Section services
section#services {
	width: 100%;
	.container-flex {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		.container-item {
			width: 100%;
			padding: 20px 20px;
			color: $white;
			min-height: 300px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			background-position: 0px 20px;
			-webkit-background-size: cover;
			background-size: cover;
			background-repeat: no-repeat;
			overflow: hidden;
			.item { 
				h3.title-item {
					font-size: get-size(map25);
				}
				.line {
					width: 125px;
					height: 1px;
					background-color: $white;
					margin-top: 9px;
					margin-bottom: 13px;
				}
				a.link-item {
					font-size: get-size(map14);
					color: $white;
					&:hover {
						text-decoration: underline !important;
					}
					svg {
						margin-left: 5px;
					}
				}
				p.text-item {				
					margin-bottom: 20px;
				}	
			}
			&:nth-child(1), &:nth-child(5) {
				background-color: $skyblue;
			}
			&:nth-child(2), &:nth-child(4) {
				background-color: #eaeaea;
				color: $darken;
				.line {
					background-color: $darken;
				}
				a.link-item {
					color: $darken;
				}
			}
			&:nth-child(4) {
				-webkit-background-size: 0 0;
				background-size: 0 0;
			}
			&:nth-child(3), &:nth-child(6) {
				background-color: $blue;
			}
			@include bp-sm {
				width: 50%;
				&:nth-child(4) {
					background-color: $skyblue;
					color: $white;
					.line {
						background-color: $white;
					}
					a.link-item {
						color: $white;
					}
				}
				&:nth-child(5) {
					background-color: #eaeaea;
					color: $darken;
					.line {
						background-color: $darken;
					}
					a.link-item {
						color: $darken;
					}
				}
			}
			@include bp-md {
				width: percentage(1/3);
				&:nth-child(1), &:nth-child(5) {
					background-color: $skyblue;
					color: $white;
					.line {
						background-color: $white;
					}
					a.link-item {
						color: $white;
					}
				}
				&:nth-child(2), &:nth-child(6) {
					background-color: #eaeaea;
					color: $darken;
					.line {
						background-color: $darken;
					}
					a.link-item {
						color: $darken;
					}
				}
				&:nth-child(6) {
					-webkit-background-size: 0 0;
					background-size: 0 0;
				}
				&:nth-child(4) {
					-webkit-background-size: cover;
					background-size: cover;
					background-color: $blue;
					color: $white;
					.line {
						background-color: $white;
					}
					a.link-item {
						color: $white;
					}
				}
			}
		}
	}
}

#blog-posts {
	width: 100%;
	text-align: center;
	padding: 60px 0px;
	h2 {
		font-weight: normal;
	}
	.container-thumbnail {
		position: relative;
		width: 100%;
		padding-bottom: 75%;
		overflow: hidden;
		.featured-thumbnail {
			position: absolute;
			padding: 0px 15px;
			width: 100%;
			height: 100%;
			background-position: center;
			-webkit-background-size: cover;
			background-size: cover;
			transition: 1s all ease-out;
		}
		&:hover .featured-thumbnail {
			transform: scale(1.1);
		}
	}
	.article-container {
		text-align: left;
		margin-top: 35px;
		overflow: hidden;
		a {
			text-decoration: none;
			h3.title-article {
				font-size: get-size(map16);
				color: $blue;
				margin-top: 20px;
			}
		}
		p.excerpt-article {
			color: $darken;
			margin-bottom: 10px;
		}
		.link-article {
			font-size: get-size(map14);
			color: $skyblue;
			&:hover {
				text-decoration: underline !important;
			}
		}
	}
}